import axios from 'axios'
import React from 'react'
import qs from 'querystring'
import './index.css'
export default function Register(props) {
    const nameRef=React.useRef()
    const passwordRef=React.useRef()
    const rePasswordRef=React.useRef()
    const clickRegister=()=>{
        const params={username:nameRef.current.value,password:passwordRef.current.value}
       if(passwordRef.current.value!==rePasswordRef.current.value){
alert('两次输入的密码不一致！')
return 
       }
       if(params.username===''){
           alert('用户名不能为空')
           return 
       }
       if(params.password===''){
        alert('密码不能为空')
        return 
    }
        axios.post('http://aays.top:4000/user/register',qs.stringify(params)).then(
res=>{
    nameRef.current.value=passwordRef.current.value=rePasswordRef.current.value=''
    alert(res.data)
    if(res.data==='注册成功'){
        props.history.push('/user/login')
    }
},
err=>{
    alert('注册失败')
    console.log('注册失败',err);
}
        )
    }
    return (
        <div className='user-register'>
            <div className='user-title'>注册</div>
        <div className='name-input'>用户名：<input ref={nameRef} type='text' className='user-input'></input></div>
           <div className='password-input'>密码：<input ref={passwordRef} type='password' className='user-input'></input></div>
           <div className='re-Password-input'>重复密码：<input ref={rePasswordRef} type='password' className='user-input'></input></div>
          <div className='user-btn'> <button onClick={clickRegister}>注册</button></div>
        </div>
    )
}
