import React from 'react'
import './index.css'
import axios from 'axios'
import qs from 'querystring'
import PubSub from 'pubsub-js'
export default function Login(props) {
    React.useEffect(()=>{
        if(document.cookie!==''){
            const {username,password}=qs.parse(document.cookie.split(';')[0])
            nameRef.current.value=username
            passwordRef.current.value=password
        }
    },[])
    const nameRef=React.useRef()
    const passwordRef=React.useRef()
    let clickRegister=()=>{
        const params={username:nameRef.current.value,password:passwordRef.current.value}
        if(params.username===''){
            alert('用户名不能为空')
            return 
        }
        if(params.password===''){
         alert('密码不能为空')
         return 
     }
        axios.post('http://aays.top:4000/user/login',qs.stringify(params)).then(res=>{
            alert(res.data.msg)
            if(res.data.state==='success'){
                PubSub.publish('HasLogin',true)
                PubSub.publish('UserName',res.data.username)
                let date=new Date()
                date.setTime(date.getTime()+60000*0.5)
                document.cookie=`username=${params.username}&password=${params.password};expires=${date.toGMTString()}`
                props.history.push('/home')
            }
        },
        err=>{
            console.log(err);
        }
        )
    }
    return (
        <div className='user-register'>
            <div className='user-title'>登录</div>
        <div className='name-input'>用户名：<input ref={nameRef} type='text' className='user-input'></input></div>
           <div className='password-input'>密码：<input ref={passwordRef} type='password' className='user-input'></input></div>
           <div className='user-btn'> <button onClick={clickRegister}>登录</button></div>
        </div>
    )
}
